<template>
  <div id="trunk">
    <div class="nav-form">
      <el-form :inline="true" ref="form" :rules="rule" :model="form" class="demo-form-inline">
        <el-form-item label="" prop="groupName">
          <el-input v-model="form.groupName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="strategyRetry">
          <el-select v-model="form.strategyRetry" placeholder=""  style="width: 100%">
            <el-option
                :label="item.label"
                :value="item.value"
                v-for="item in policyType"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="strategyType">
          <el-select v-model="form.strategyType" placeholder=""  style="width: 100%">
            <el-option
                :label="item.label"
                :value="item.value"
                v-for="item in retryStrategyType"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="find"></el-button>
          <el-button @click="clear"></el-button>
        </el-form-item><!---->
      </el-form>
      <el-button type="primary" @click="showAddForm(null, '')"
      >
      </el-button
      >
    </div>

    <el-dialog :title="title" :visible.sync="dialogFormVisible">
      <el-form :model="addFrom" ref="addForm" :rules="rules">
        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="groupName"
          >
            <el-input v-model="addFrom.groupName" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth" prop="groupPrefix">
            <el-input v-model="addFrom.groupPrefix"></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="strategyRetry"
          >
            <el-select v-model="addFrom.strategyRetry" placeholder=""   style="width: 100%">
              <el-option
                  v-for="item in retryStrategyType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="strategyType"
          >
            <el-select v-model="addFrom.strategyType" placeholder=""  style="width: 100%"  >
              <el-option
                  v-for="item in policyType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </div>

        <el-form-item style="margin-top: 20px" :label="'' + (index + 1)" :label-width="formLabelWidth"
                      :key="addFrom.pbxGwgroupGatewayList.key"
                      v-for="(pbxGwgroupGatewayList, index) in addFrom.pbxGwgroupGatewayList"
                      :prop="'pbxGwgroupGatewayList.' + index + '.gatewayId'"
                      :rules="{
                      required: true, message: ',', trigger: 'blur'
            }">
          <div class="width">
            <el-select v-model="pbxGwgroupGatewayList.gatewayId" placeholder="">
              <el-option
                  v-for="item in pbxList"
                  :key="item.id"
                  :label="item.gatewayName"
                  :value="item.id">
              </el-option>
            </el-select>
            <div style="padding: 0 20px">
              <el-input class="el-input" v-model="pbxGwgroupGatewayList.weight"></el-input>
            </div>
            <div>
              <!--              <el-button @click="addGateway"></el-button>-->
              <!--              <el-button style="margin-left: 20px; vertical-align: top" @click="removeClick(pbxGwgroupGatewayList)"></el-button>-->

              <el-tooltip style="margin-left: 20px; vertical-align: top" @click.native="addGateway" class="item"
                          effect="dark" content="" placement="bottom">
                <el-button><i class="icon iconfont icon-jia"></i></el-button>
              </el-tooltip>
              <el-tooltip style="margin-left: 20px; vertical-align: top"
                          @click.native="removeClick(pbxGwgroupGatewayList)" class="item" effect="dark" content=""
                          placement="bottom">
                <el-button><i class="icon iconfont icon-shanchu2"></i></el-button>
              </el-tooltip>

            </div>
          </div>
        </el-form-item>

        <el-form-item
            label=""
            :label-width="formLabelWidth"
        >
          <el-input
              v-model="addFrom.remark"
              placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm()"
        > 
        </el-button
        >
      </div>
    </el-dialog>

    <el-table border :data="list" style="width: 100%" v-if="list.length !== 0" >
      <el-table-column align="center" prop="index" label="" width="50">
        <template scope="scope">{{ scope.$index + 1 }}</template>
      </el-table-column>
      <el-table-column align="center" prop="groupName" label="">
        <template scope="scope">
          {{ scope.row.groupName }}
        </template>
      </el-table-column>
      <el-table-column align="center" prop="pbxGwgroupGatewayList" label="">
        <template scope="scope">
          <span v-if="scope.strategyType === 0"></span>
          <span v-else></span>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="pbxGwgroupGatewayList" label="">
        <template scope="scope">
          <span v-if="scope.strategyRetry === 0"></span>
          <span v-else></span>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="retryStrategy" label="">
        <template scope="scope">
          <div class="table" v-for="(item, i) in scope.row.pbxGwgroupGatewayList">
            <p>
              <span>{{ scope.row.pbxGwgroupGatewayList[i].gatewayName }}</span>
              --<span>{{ scope.row.pbxGwgroupGatewayList[i].weight }}</span>
            </p>
          </div>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="remark" label="">
        <template scope="scope">
          <el-link
              style="margin-right: 20px"
              @click="showAddForm(scope.row, '')"
              type="info"
          >
          </el-link
          >
          <el-link @click="removeIt(scope.row)" style="margin-right: 20px" type="info"></el-link>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="remark" label="">
        <template scope="scope">
          {{ scope.row.remark || '' }}
        </template>
      </el-table-column>
    </el-table>
    <my-empty v-else/>
  </div>
</template>

<script>
import {addGwgroup, deleteGwgroup, getGwgroup, getPbxAll, upDateGwgroup} from "@/newwork/ground-colltroner";
import myEmpty from "@/newwork/myEmpty";
import {isValidNumber} from "@/util/validate";

export default {
  name: 'trunkGroup',
  components: {
    myEmpty
  },
  data() {
    const validateNum = (rule, value, callback) => {
      if (!isValidNumber(value)) {
        callback(new Error('ip,'))
      } else {
        callback()
      }
    }
    return {
      form: {
        groupName: '',
        strategyRetry: '',
        strategyType: ''
      },
      title: '',
      dialogFormVisible: false,
      edit: false,
      pbxList: [],
      addFrom: {
        groupPrefix: '',
        groupName: '', //
        strategyRetry: '', //
        strategyType: '', // 
        pbxGwgroupGatewayList: '', //
        totalWeight: 0, //
        pageNum: 1,
        pageSize: 10,
      },
      list: [],
      startTime: '',
      formLabelWidth: '120px',
      policyType: [
        {label: '', value: 0},
        {label: '', value: 1}
      ],
      retryStrategyType: [
        {label: '', value: 0},
        {label: '', value: 1}
      ],
      rules: {
        groupName: [
          {required: true, message: ',', trigger: 'blur'}
        ],
        groupPrefix: [
          {required: true, message: ',', trigger: 'blur'},
          {validator: validateNum, message: '', trigger: 'blur'}
        ],
        strategyRetry: [
          {required: true, message: ',', trigger: 'blur'}
        ],
        strategyType: [
          {required: true, message: ',', trigger: 'blur'}
        ],
        retryStrategy: [
          {required: true, message: ',', trigger: 'blur'}
        ],
      },
      rule: {
        groupName: [
          {required: false, message: ',', trigger: 'blur'}
        ],
        strategyRetry: [
          {required: false, message: ',', trigger: 'blur'}
        ],
        strategyType: [
          {required: false, message: ',', trigger: 'blur'}
        ],
      },
      value: '',
      tableData: [],
      gateway: {gatewayId: '', weight: ''},
    }
  },
  methods: {
    getGwgroup(form) {
      getGwgroup(form).then(res => {
        this.$store.dispatch('total', res.data.data.total)
        this.list = res.data.data.records
      }).catch(e => {
        this.$message.error(e)
      })
    },
    tableRowClassName({row, rowIndex}) {
      row.index = rowIndex
    },

    showAddForm(row, title) {
      this.dialogFormVisible = true
      this.title = title
      if (title === '') {
        this.addFrom = row
      } else {
        this.addFrom = this.$options.data().addFrom
        //
        this.gateway = this.$options.data().gateway
        this.tableData.push(this.gateway)
      }
    },
    find() {
      this.getGwgroup(this.form)
    },
    clear() {
      this.resetForm('form')
      this.getGwgroup(this.form)
    },
    removeClick(i) {
      let index = this.addFrom.pbxGwgroupGatewayList.indexOf(i)
      if (index !== -1) {
        this.addFrom.pbxGwgroupGatewayList.splice(index, 1)
      }
    },
    addGateway() {
      this.addFrom.pbxGwgroupGatewayList.push({
        gatewayId: '',
        weight: '',
        key: Date.now()
      })
    },
    submitForm() {
      this.$refs['addForm'].validate((valid) => {
        if (valid) {
          this.title === '' ? addGwgroup(this.addFrom).then(res => {
                console.log(res)
                if (res.data.code === 200) {
                  this.$message.success('')
                  this.dialogFormVisible = false
                  this.getGwgroup(this.form)
                } else {
                  this.$message.error(res.data.msg)
                }
              }).catch(e => {
                this.$message.error(e)
              }) :
              upDateGwgroup(this.addFrom).then(res => {
                if (res.data.code === 200) {
                  this.$message.success('')
                  this.dialogFormVisible = false
                } else {
                  this.$message.error(res.data.msg)
                }
              })
        } else {
          this.$message.error(' ')
          return false
        }
      })
    },
    resetForm(type) {
      type = 'form' ? this.$refs['form'].resetFields() : this.$refs['addForm'].resetFields()
    },
    removeIt(row) {
      deleteGwgroup(row.id).then(res => {
        if (res.data.code === 200) {
          this.$message.success('')
          this.getGwgroup(this.from)
        } else {
          this.$message.error(res.data.msg)
        }
      }).catch(e => {
        this.$message.error(e)
      })
    },
    upEdit() {
      this.edit = !this.edit
      // this.tableData.gatewayId = this.addFrom.pbxGatewayList
    }

  },

  created() {
    this.form = this.$store.state.formPage
    this.getGwgroup(this.form)
  },
  mounted() {
    this.$bus.$on('pageChange', () => {
      this.form = this.$store.state.formPage
      this.getGwgroup(this.form)
    })
  },
  watch: {
    dialogFormVisible(val) {
      console.log(val)
      if (val === true) {
        getPbxAll().then(res => {
          this.pbxList = res.data.data.records
        })
      } else {
        this.tableData = this.$options.data().tableData
        this.edit = false
        this.getGwgroup(this.form)
      }
    },
    tableData: {
      handler: function (val) {
        this.addFrom.pbxGwgroupGatewayList = val
      },
      deep: true
    },
  }
}
</script>

<style scoped>

.container p {
  background-color: #f2f2f2;
  padding: 10px 15px;
}

.nav-form {
  margin: 15px;
  height: 40px;
  display: flex;
  justify-content: space-between;
}

#trunk .width {
  display: flex;
  justify-content: space-between;
}

.width {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.el-input {
  display: inline-block;
}

.width > * {
  flex: 1;
}

ul {
  width: 80%;
  flex-direction: column;
}

ul li {
  display: flex;
  list-style: none;
  margin: 10px 0;
}

.li-input {
  width: 50%;
  margin-left: 20px;
}

.table p {
  background-color: transparent;
}

.tables {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tables p {
  width: 100%;
}

.tables > div {
  text-align: center;
  flex: 1;
}

.tables p {
  border: 1px solid #f3f3f3;
}

.li-input {
  margin: 0 10px;
}

</style>
