<template>
  <div id="camera">
    <div class="camera-l">
      <e-tree />
    </div>
    <div class="container">
      <p class="title"></p>
      <div class="nav-form">
        <el-form :inline="true" :model="form" class="demo-form-inline">
          <el-form-item label="">
            <el-input
              v-model="form.serialNumber"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.deviceName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.stauts" placeholder="">
              <el-option
                v-for="item in stautsData"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.company" placeholder="">
              <el-option
                v-for="item in equipmentManufacturerData"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.type" placeholder="">
              <el-option
                v-for="item in typeData"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click=""></el-button>
          </el-form-item>
        </el-form>
        <div>
          <el-button type="primary" @click="showAddForm(null, '')"
            ></el-button
          >
          <el-button type="primary"></el-button>
          <el-button type="primary"></el-button>
        </div>
      </div>

      <el-dialog :title="title" :visible.sync="dialogTableVisible">
        <el-form :rules="rules" ref="ruleForm" :model="addForm">
          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="name"
            >
              <el-input
                v-model="addForm.name"
                autocomplete="off"
                :readonly="isReadOnly"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="SIP"
              :label-width="formLabelWidth"
              prop="serialNumber"
            >
              <el-input
                v-model="addForm.serialNumber"
                autocomplete="off"
                :readonly="isReadOnly"
              ></el-input>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="company"
            >
              <el-select
                v-model="addForm.company"
                placeholder=""
                :disabled="isReadOnly"
              >
                <el-option
                  v-for="item in equipmentManufacturerData"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="type"
            >
              <el-select
                v-model="addForm.type"
                placeholder=""
                :disabled="isReadOnly"
              >
                <el-option
                  v-for="item in typeData"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item label="IP" :label-width="formLabelWidth">
              <el-input
                v-model="addForm.ip"
                autocomplete="off"
                :readonly="isReadOnly"
              ></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input
                v-model="addForm.host"
                autocomplete="off"
                :readonly="isReadOnly"
              ></el-input>
            </el-form-item>
          </div>

          <div class="width">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input
                v-model="addForm.account"
                autocomplete="off"
                :readonly="isReadOnly"
              ></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input
                v-model="addForm.password"
                autocomplete="off"
                :readonly="isReadOnly"
              ></el-input>
            </el-form-item>
          </div>

          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="area"
            >
              <el-cascader
                :disabled="isReadOnly"
                v-model="addForm.area"
                :options="area"
                :props="{ expandTrigger: 'hover' }"
              ></el-cascader>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="createTime" :disabled="true"></el-input>
            </el-form-item>
          </div>
          <el-form-item label="" :label-width="formLabelWidth">
            <div v-if="addForm.address.x != ''">
              {{ this.addForm.address.province }}
              {{ this.addForm.address.city }}
              {{ this.addForm.address.district }}
              {{ this.addForm.address.street }}
            </div>
            <div v-else></div>
            <baiduMap
              id="baidu-map"
              ak="qF2OlD0Gin1GwwqYTHR3cQ8dqfcfZhtZ"
              center=""
            >
              <bm-view style="width: 100%; height: 400px; flex: 1"></bm-view>
              <Geolocation
                :autoLocation="true"
                @locationSuccess="locationSuccess"
              ></Geolocation>
            </baiduMap>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogTableVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm('ruleForm')"
            > </el-button
          >
        </div>
      </el-dialog>
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="date" label="" width="100">
          <template scope="scope">
            {{ scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" width="100">
        </el-table-column>
        <el-table-column prop="serialNumber" label="SIP" width="180">
        </el-table-column>
        <el-table-column prop="company" label="" width="180">
        </el-table-column>
        <el-table-column prop="ip" label="IP" width="180">
        </el-table-column>
        <el-table-column prop="area" label="" width="180">
        </el-table-column>
        <el-table-column prop="createTime" label="" width="180">
        </el-table-column>
        <el-table-column prop="address" label="" width="300">
          <template scope="scope">
            <div>
              {{ scope.row.address.province }}{{ scope.row.address.city
              }}{{ scope.row.address.district }}{{ scope.row.address.street }}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="stauts" label="" width="180">
          <template scope="scope">
            <div v-if="scope.row.stauts"></div>
            <div v-else></div>
          </template>
        </el-table-column>
        <el-table-column prop="stauts" label="" width="">
          <template scope="scope">
            <el-link
              class="a-link"
              type="info"
              @click="showAddForm(scope.row, '')"
              ></el-link
            >
            <el-link class="a-link" type="info" @click="removeIt(scope.row)"
              ></el-link
            >
            <el-link
              class="a-link"
              type="info"
              @click="showAddForm(scope.row, '')"
              ></el-link
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import eTree from '../../../components/eTree.vue'
import baiduMap from 'vue-baidu-map/components/map/Map'
import Geolocation from 'vue-baidu-map/components/controls/Geolocation.vue'
import bmView from 'vue-baidu-map/components/map/MapView.vue'
import { getNowFormatDate } from '@/uti'
export default {
  name: 'cameraView',
  components: {
    eTree,
    baiduMap,
    Geolocation,
    bmView
  },
  data() {
    return {
      title: '',
      isReadOnly: false,
      dialogTableVisible: false,
      formLabelWidth: '120px',
      form: {
        serialNumber: '', //
        deviceName: '', //
        company: '', //
        ip: '', //
        equipmentManufacturer: '',
        type: '',
        stauts: ''
      },
      addForm: {
        name: '',
        serialNumber: '',
        company: '',
        type: '',
        ip: '',
        host: '',
        account: '', //
        password: '',
        area: '',
        createTime: '',
        address: {
          x: '',
          y: ''
        },
        location: '',
        stauts: true
      },
      list: [],
      stautsData: [
        { label: '', value: '' },
        { label: '', value: '' }
      ],
      equipmentManufacturerData: [
        { label: '', value: '' },
        { label: '', value: '' }
      ],
      typeData: [
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' },
        { label: '', value: '' }
      ],
      area: [
        {
          value: 'zhinan',
          label: '',
          children: [
            {
              value: 'shejiyuanze',
              label: '',
              children: [
                {
                  value: 'yizhi',
                  label: ''
                },
                {
                  value: 'fankui',
                  label: ''
                },
                {
                  value: 'xiaolv',
                  label: ''
                },
                {
                  value: 'kekong',
                  label: ''
                }
              ]
            },
            {
              value: 'daohang',
              label: '',
              children: [
                {
                  value: 'cexiangdaohang',
                  label: ''
                },
                {
                  value: 'dingbudaohang',
                  label: ''
                }
              ]
            }
          ]
        },
        {
          value: 'zujian',
          label: '',
          children: [
            {
              value: 'basic',
              label: 'Basic',
              children: [
                {
                  value: 'layout',
                  label: 'Layout '
                },
                {
                  value: 'color',
                  label: 'Color '
                },
                {
                  value: 'typography',
                  label: 'Typography '
                },
                {
                  value: 'icon',
                  label: 'Icon '
                },
                {
                  value: 'button',
                  label: 'Button '
                }
              ]
            },
            {
              value: 'form',
              label: 'Form',
              children: [
                {
                  value: 'radio',
                  label: 'Radio '
                },
                {
                  value: 'checkbox',
                  label: 'Checkbox '
                },
                {
                  value: 'input',
                  label: 'Input '
                },
                {
                  value: 'input-number',
                  label: 'InputNumber '
                },
                {
                  value: 'select',
                  label: 'Select '
                },
                {
                  value: 'cascader',
                  label: 'Cascader '
                },
                {
                  value: 'switch',
                  label: 'Switch '
                },
                {
                  value: 'slider',
                  label: 'Slider '
                },
                {
                  value: 'time-picker',
                  label: 'TimePicker '
                },
                {
                  value: 'date-picker',
                  label: 'DatePicker '
                },
                {
                  value: 'datetime-picker',
                  label: 'DateTimePicker '
                },
                {
                  value: 'upload',
                  label: 'Upload '
                },
                {
                  value: 'rate',
                  label: 'Rate '
                },
                {
                  value: 'form',
                  label: 'Form '
                }
              ]
            },
            {
              value: 'data',
              label: 'Data',
              children: [
                {
                  value: 'table',
                  label: 'Table '
                },
                {
                  value: 'tag',
                  label: 'Tag '
                },
                {
                  value: 'progress',
                  label: 'Progress '
                },
                {
                  value: 'tree',
                  label: 'Tree '
                },
                {
                  value: 'pagination',
                  label: 'Pagination '
                },
                {
                  value: 'badge',
                  label: 'Badge '
                }
              ]
            },
            {
              value: 'notice',
              label: 'Notice',
              children: [
                {
                  value: 'alert',
                  label: 'Alert '
                },
                {
                  value: 'loading',
                  label: 'Loading '
                },
                {
                  value: 'message',
                  label: 'Message '
                },
                {
                  value: 'message-box',
                  label: 'MessageBox '
                },
                {
                  value: 'notification',
                  label: 'Notification '
                }
              ]
            },
            {
              value: 'navigation',
              label: 'Navigation',
              children: [
                {
                  value: 'menu',
                  label: 'NavMenu '
                },
                {
                  value: 'tabs',
                  label: 'Tabs '
                },
                {
                  value: 'breadcrumb',
                  label: 'Breadcrumb '
                },
                {
                  value: 'dropdown',
                  label: 'Dropdown '
                },
                {
                  value: 'steps',
                  label: 'Steps '
                }
              ]
            },
            {
              value: 'others',
              label: 'Others',
              children: [
                {
                  value: 'dialog',
                  label: 'Dialog '
                },
                {
                  value: 'tooltip',
                  label: 'Tooltip '
                },
                {
                  value: 'popover',
                  label: 'Popover '
                },
                {
                  value: 'card',
                  label: 'Card '
                },
                {
                  value: 'carousel',
                  label: 'Carousel '
                },
                {
                  value: 'collapse',
                  label: 'Collapse '
                }
              ]
            }
          ]
        },
        {
          value: 'ziyuan',
          label: '',
          children: [
            {
              value: 'axure',
              label: 'Axure Components'
            },
            {
              value: 'sketch',
              label: 'Sketch Templates'
            },
            {
              value: 'jiaohu',
              label: ''
            }
          ]
        }
      ],
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        serialNumber: [
          { required: true, message: '', trigger: 'blur' }
        ],
        company: [
          { required: true, message: '', trigger: 'blur' }
        ],
        type: [{ required: true, message: '', trigger: 'blur' }],
        area: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.list.push(this.addForm)
          this.dialogTableVisible = false
          window.localStorage.setItem('camera', JSON.stringify(this.list))
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message({
            message: ' ',
            type: 'error'
          })
          return false
        }
      })
    },
    showAddForm(row, title) {
      this.dialogTableVisible = true
      this.title = title
      if (title === '') {
        this.addForm = row
      } else if (title === '') {
        this.addForm = row
        this.isReadOnly = true
      } else {
        this.resetForm()
      }
    },
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex
    },

    removeIt(row) {
      this.list.map((item, i) => {
        if (row === item) {
          this.list.splice(i, 1)
        }
      })
    },
    locationSuccess(e) {
      this.addForm.address.x = e.marker.map.SC.lat
      this.addForm.address.y = e.marker.map.SC.lng
      console.log(e)
      this.addForm.address = e['addressComponent']
      console.log(this.addForm)
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  },
  created() {
    this.list = JSON.parse(window.localStorage.getItem('camera')) || []
  },
  computed: {
    createTime(value) {
      return (this.addForm.createTime = getNowFormatDate(value))
    }
  }
}
</script>

<style scoped>
#camera {
  display: flex;
  justify-content: space-between;
}

.container {
  flex: 1;
  border: 1px solid #ccc;
  margin: 0 20px;
}

.camera-l {
  width: 300px;
}

.container p {
  background-color: #f2f2f2;
  padding: 10px 15px;
}

.nav-form {
  margin: 15px;
  display: flex;
  justify-content: space-between;
}

.width {
  display: flex;
}

.width > * {
  width: 45%;
  margin-right: 20px;
}
#baidu-map {
  margin-top: 20px;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
}
.a-link {
  margin-right: 20px;
}
</style>
