<template>
  <!--  -->
  <div>
    <div >
      <div class="nav-form">
        <el-form :inline="true" :model="from" class="demo-form-inline">
          <el-form-item>
          </el-form-item>
        </el-form>
        <a>         <el-button type="primary" @click="black"
        >
        </el-button
        >
          <el-button type="primary" @click="showAddForm(null, '')"
          >
          </el-button
          ></a>
      </div>

      <el-dialog :title="title" :visible.sync="dialogFormVisible">
        <el-form ref="addForm" :model="addForm" :rules="riles">
          <el-form-item label="" :label-width="formLabelWidth" prop="name">
            <el-input v-model="addForm.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" prop="orderNum">
            <el-input v-model="addForm.orderNum" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item
              label=""
              prop="code"
              :label-width="formLabelWidth"
          >
            <el-input v-model="addForm.code">aqa</el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio-group v-model="addForm.status">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitAddForm('addForm')"
          > 
          </el-button
          >
        </div>
      </el-dialog>

      <el-table :data="resultList" border style="width: 100%">
        <el-table-column  align="center"  prop="name" label='' width="50">
          <template scope="scope">{{scope.$index + 1}}</template>
        </el-table-column>
        <el-table-column  align="center"  prop="name" label=''></el-table-column>
        <el-table-column  align="center"  prop="createBy" label=""></el-table-column>
        <el-table-column  align="center"  prop="status" label="">
          <template scope="scope">
            <div v-if="scope.row.status === 0"> <el-tag type="success"></el-tag></div>
            <div v-else> <el-tag type="success"></el-tag></div>
          </template>
        </el-table-column>
        <el-table-column  align="center"  prop="createTime" label=""></el-table-column>
        <el-table-column  align="center"  prop="mark" label="">
          <template scope="scope">
            <div class="operate">
              <a style="margin-right: 20px" @click="showAddForm(scope.row, '')"></a>
              <a style="margin-right: 20px" @click="removeIt(scope.row.id)"></a>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>


  </div>
</template>

<script>
import {getNowFormatDate} from '@/uti'
import {
  getDictionaryAll,
  addDictionary,
  upData,
  removeDictionary
} from "@/newwork/system-colltroner";

export default {
  name: 'serve-manage',
  data() {
    return {
      isShow: false,
      title: '',
      from: {
        searchInp: '',
      },
      formLabelWidth: '120px',
      dialogFormVisible: false,
      resultList: [],
      showData: '',
      addForm: {
        name: '',  //
        creatTime: '',  //
        code: '',    //
        pageNum: 1,  //
        pageSize: 10,  //
        orderNum: '', //
        typeId: '',  // typeId
        status: 0, // 
      },
      codeList: [],
      riles: {
        name: [{required: true, message: '', trigger: 'blur'}],
        desc: [{required: true, message: '', trigger: 'blur'}],
        mark: [{required: true, message: '', trigger: 'blur'}],
        orderNum:  [{required: true, message: '', trigger: 'blur'}],
        code:  [{required: true, message: '', trigger: 'blur'}]
      },
    }
  },
  computed: {
    creatTime() {
      return (this.addForm.creatTime = getNowFormatDate())
    },
    code() {
      return  this.$route.query.name
    }

  },
  methods: {
    toPath() {
      this.$router.push({name: 'dataManage'})
    },
    disabled(status){
      if(status == 1){
        return false
      }else {
        return  true
      }
    },
    submitAddForm() {
      this.$refs['addForm'].validate((valid) => {
        const data = {}
        data.pageNum = 1
        data.pageSize = 10
        data.code = this.$route.query.code
        if (valid) {
          //
          this.addForm.typeId = this.$route.query.id

          /*
          *
          *     addDictionary  upData
          * */
         this.title === '' ?  upData(this.addForm).then(res => {
           if(res.data.code === 200){
             this.$message.success('')
             this.getDictionaryAll(data)
           }else {
             this.$message.error(res.data.msg)
           }
             }).catch(e => {
               this.$message.error(e)
             }):
             addDictionary(this.addForm).then(res => {
               if(res.data.code === 200){
                 this.$message.success('')
                 this.getDictionaryAll(data)

               }else {
                 this.$message.error(res.data.msg)
               }
          })
          this.dialogFormVisible = false
        }
      })
    },
    showAddForm(row, type) {
      this.title = type
      this.dialogFormVisible = true
      type === '' ? (this.addForm = row) : ''
    },
    black() {
      this.$router.go(-1)
    },
    removeIt(id) {
        console.log(id)
      removeDictionary(id).then(res => {
          if(res.data.code === 200){
            this.$message.success('')
            this.getDictionaryAll(this.addForm )
          }
      }).catch(e => {
        this.$message.error(e)
      })
    },

    getDictionaryAll(id){
      getDictionaryAll(id).then(res => {
        console.log(res)
        this.resultList = res.data.data
      }).catch(e => {
        this.$message.error(e)
      })
    }
  },
  created() {
    this.addForm.code = this.$route.query.code
    this.getDictionaryAll(this.addForm )
  },
  watch: {
    dialogFormVisible(val){
      if(!val) {
        this.addForm = this.$options.data().addForm
      }
    }
  }
}
</script>

<style scoped>
.container {
  border: 1px solid #ccc;
  margin: 0 20px;
}

.container p {
  background-color: #f2f2f2;
  padding: 10px 15px;
}

.nav-form {
  margin: 15px;
  height: 40px;
  display: flex;
  justify-content: space-between;
}

#networkManagement .width {
  display: flex;
  justify-content: space-between;
}

.width > * {
  flex: 1;
}

.mask-btn {
  margin-left: 20px;
}
</style>
