<template>
  <div id="networkManagement">
    <div class="nav-form">
      <el-form  :model="form" :hide-required-asterisk="true" :show-message="false" :inline="true" class="demo-form-inline" ref="form" :rules="rule" >
        <el-form-item :span="5" label="" prop="gatewayName">
          <el-input v-model="form.gatewayName"></el-input>
        </el-form-item>
        <el-form-item :span="1000000" label="IP" prop="gatewayIp">
          <el-input v-model="form.gatewayIp" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="gatewayType">
          <el-select v-model="form.gatewayType" placeholder="">
            <el-option
                :label="item.label"
                :value="item.value"
                v-for="item in relayType"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
                    <el-button type="primary" @click="find"></el-button>
                    <el-button @click="clear(form)"></el-button>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="showAddForm(null, '')"
      >
      </el-button
      >
    </div>
    <el-dialog :title="title" :visible.sync="dialogFormVisible">
      <el-form :model="addFrom" ref="formName" :rules="rules">
        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="gatewayName"
          >
            <el-input v-model="addFrom.gatewayName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="gatewayType"
          >
            <el-select v-model="addFrom.gatewayType" placeholder=""style="width: 100%" >
              <el-option
                :label="item.label"
                :value="item.value"
                v-for="item in relayType"
              ></el-option>
            </el-select>
          </el-form-item>

        </div>
        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="realm"
          >
            <el-input
                v-model="addFrom.realm"
                autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="proxyIp"
          >
            <el-input
                v-model="addFrom.proxyIp"
                autocomplete="off"
            ></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item
            v-if="title === ''"
              label=""
              :label-width="formLabelWidth"
              prop="codecs"
          >
            <el-select v-model="codecs"
                       @change="codecsBlur"
                       multiple
                       style="width: 100%"
                       collapse-tags>
              <el-option
                  v-for="item in dictionaryList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.name">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              required
          >
            <el-select v-model="addFrom.dtmfMode" style="width: 100%" >
              <el-option
                  v-for="item in dtmfModeData"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item
            v-if="title === ''"
              label=""
              :label-width="formLabelWidth"
              prop="expires"
          >
            <el-input
                v-model="addFrom.expires"
                autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item
              label="IP"
              :label-width="formLabelWidth"
              prop="gatewayIp"
          >
            <el-input
                v-model="addFrom.gatewayIp"
                autocomplete="off"
            ></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="gatewayPort"
          >
            <el-input
                v-model="addFrom.gatewayPort"
                autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="profileId"
          >
            <el-select v-model="addFrom.profileId" placeholder="" style="width: 100%">
              <el-option
                  v-for="item in profileIdList"
                  :key="item.id"
                  :label="item.netName"
                  :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </div>
        <div class="width">

          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="maxCall"
          >
            <el-input v-model="addFrom.maxCall"/>
          </el-form-item>
          <el-form-item  prop="accountUser" label="" :label-width="formLabelWidth">
            <el-input :disabled="addFrom.gatewayType === 1" v-model="addFrom.accountUser"></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="username"
          >
            <el-input v-model="addFrom.username"/>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="password"
          >
            <el-input v-model="addFrom.password"/>
          </el-form-item>
        </div>

        <div class="width">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="register"
          >
            <el-radio-group v-model="addFrom.register">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('addFrom')"
        > 
        </el-button
        >
      </div>
    </el-dialog>

    <el-table :data="list" style="width: 100%" border>
      <el-table-column align="center" prop="date" label="" width="50px">
        <template scope="scope">{{ scope.$index + 1 }}</template>
      </el-table-column>
      <el-table-column align="center" prop="gatewayName" label=""></el-table-column>
      <el-table-column align="center" prop="accountUser" label=""></el-table-column>
<!--      <el-table-column align="center" prop="realm" label=""></el-table-column>-->
<!--      <el-table-column align="center" prop="proxyIp" label=""></el-table-column>-->
      <el-table-column align="center" prop="codecs" label=""></el-table-column>
<!--      <el-table-column align="center" prop="dtmfMode" label="dtmfMode"></el-table-column>-->
      <el-table-column align="center" prop="expires" label="(s)"></el-table-column>
      <el-table-column align="center" prop="gatewayIp" label="IP"></el-table-column>
      <el-table-column align="center" prop="gatewayPort" label=""></el-table-column>
<!--      <el-table-column align="center" prop="profileId" label="ID"></el-table-column>-->
      <el-table-column align="center" prop="gatewayType" label="">
        <template scope="scope" >
          <a v-if="scope.row.gatewayType === 0"></a>
          <a v-else-if="scope.row.gatewayType === 1"></a>
          <a v-else-if="scope.row.gatewayType === 2"></a>
          <a v-else></a>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="maxCall" label=""></el-table-column>
      <el-table-column align="center" prop="username" label=""></el-table-column>
      <el-table-column align="register" prop="username" label="">
        <template scope="scope">
          <div v-if="scope.row.register === 1"> </div>
          <div v-else> </div>
        </template>
      </el-table-column>
      <el-table-column prop="recording" align="center" label="">
        <template scope="scope">
          <div class="operate">
            <el-link
                class="a-link"
                @click="showAddForm(scope.row, '')"
                type="info"
            >
            </el-link
            >
            <el-link class="a-link" @click="removeIt(scope.row)" type="info"
            >
            </el-link
            >
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {deletePbx, getPbxAdd, getPbxAll, getProfileInfo, upDatePbx} from "@/newwork/ground-colltroner";
import {addDictionaryList, getDictionaryAll} from "@/newwork/system-colltroner";
import {isValidIP, isValidNumber, isValidPost} from "@/util/validate";


export default {
  name: 'networkManagement',
  data() {

    /**
     *
     * ip
     * @param rule  
     * @param value 
     * @param callback  
     */
    const validateIP = (rule, value, callback) => {
      if(!isValidIP(value)){
        callback(new Error('ip,'))
      }else {
        callback()
      }
    }
    const validatePost =  (rule, value, callback) => {
      if(!isValidPost(value)){
        callback(new Error('ip,'))
      }else {
        callback()
      }
    }
    const validateNum = (rule, value, callback) => {
      if(!isValidNumber(value)){
        callback(new Error('ip,'))
      }else {
        callback()
      }
    }
    return {
      dialogFormVisible: false,
      formLabelWidth: '120px',
      title: '',
      form: {
        gatewayName: '',
        gatewayIp: '',
        gatewayType: '',
        pageNum: 1,
        pageSize: 10,
      },
      list: [],
      dtmfModeData: [],
      dictionaryList: [],//''
      profileIdList:[], // ID
      codecs: [],
      addFrom: {
        accountUser: '', //
        codecs: '', //
        dtmfMode: '', //dtmfMode
        expires: '', //
        gatewayIp: '', //IP
        gatewayName: '', //
        gatewayPort: '', //
        gatewayType: '', //
        maxCall: '', //
        password: '', //
        profileId: '', //profileId
        proxyIp: '', //IP
        realm: '', //
        register: 1, // 0/ 1 
        username: '', //
      },
      codecsList: [],
      relayType: [
        {label: '', value: 0},
        {label: '', value: 1},
        {label: '', value: 2}
      ],
      networkStat: [
        {label: '', value: ''},
        {label: '', value: ''}
      ],
      registerTypr: [
        {label: '', value: 1},
        {label: '', value: 0},
      ],
      rule: {
        gatewayName: [
          {required: false, message: ' ', trigger: 'blur'}
        ],
        gatewayIp: [
          {required: false, message: ' ', trigger: 'blur'},
        ],
        gatewayType: [
          {required: false, message: ' ', trigger: 'blur'},
        ],
      },
      rules: {
        accountUser: [{required: false, message: ' ', trigger: 'blur'}],
        codecs: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        dtmfMode: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        expires: [
          {required: true, message: ' ', trigger: 'blur'},
          {validator: validateNum, message: '', trigger: 'blur'}
        ],
        gatewayIp: [
          {required: true, message: ' ', trigger: 'blur'},
          {validator: validateIP, message: 'IP, ', trigger: 'blur'}
        ],
        gatewayName: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        gatewayPort: [{required: true, message: ' ', trigger: 'blur'},
          {validator: validatePost, message: ', ', trigger: 'blur'}
        ],
        gatewayType: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        maxCall: [{required: true, message: ' ', trigger: 'blur'}],
        password: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        profileId: [
          {required: true, message: ' ', trigger: 'blur'}
        ],
        proxyIp: [{required: true, message: ' ', trigger: 'blur'},
          {validator: validateIP, message: 'IP, ', trigger: 'blur'}],
        realm: [{required: true, message: ' ', trigger: 'blur'}],
        register: [{required: true, message: ' ', trigger: 'blur'}],
        username: [{required: true, message: ' ', trigger: 'blur'}],
      }
    }
  },
  methods: {
    clear(){
      this.resetForm('form')
      this.getPbxAll(this.form)
    },
    find(){
      this.getPbxAll(this.form)
    },

    getPbxAll(form) {
      getPbxAll(form).then(res => {
        if(res.data.code === 200 ){
          this.$store.dispatch('total', res.data.data.total)
          this.list = res.data.data.records
        }
      }).catch(e => {
        this.$message.error(e)
      })
    },
    submitForm() {
      this.$refs['formName'].validate((valid) => {
        if (valid) {
          if (this.title === '') {
            getPbxAdd(this.addFrom).then(res => {
              if (res.data.code === 200) {
                this.$message.success('')
                this.getPbxAll(this.from)
                this.dialogFormVisible = false
              } else {
                this.$message.error(res.data.msg)
              }
            })
          } else {
            upDatePbx(this.addFrom).then(res => {
              if (res.data.code === 200) {
                this.$message.success('')
                this.getPbxAll(this.from)
                this.dialogFormVisible = false
              } else {
                this.$message.error(res.data.msg)
              }
            })
          }
          // this.resetForm('formName')
        } else {
          console.log('error submit!!')
        }
      })
    },
    resetForm(type) {
     type ==='form' ?  this.$refs['form'].resetFields() : this.$refs['formName'].resetFields();
    },
    showAddForm(row, title) {
      this.dialogFormVisible = true
      this.title = title
      if (title === '') {
        this.addFrom = row
      } else if (title === '') {
        this.addForm = row
        this.isReadOnly = true
      } else {
        // this.resetForm("formName")
      }
    },
    tableRowClassName({row, rowIndex}) {
      row.index = rowIndex
    },

    removeIt(row) {
      deletePbx(row.id).then(res => {
        if (res.data.code === 200) {
          this.$message.success('')
          this.getPbxAll(this.form)
        } else {
          this.$message.error(res.data.msg)
        }
      }).catch(e => {
        this.$message.error(e)
      })
    },
    getProfileInfo(){
      getProfileInfo().then(res => {
        if(res.data.code === 200){
          this.profileIdList = res.data.data.records
        }
      })
    },
    codecsBlur(){
     this.addFrom.codecs =  this.codecs.join(',')
    },
    /**
     * , code
     *
     * @param dictionaryTypeList
     */
    filterGateway(dictionaryTypeList){
    let _data = {}
      let _dtmfModeData = {}

      dictionaryTypeList.forEach(item => {
        if(item.name === ''){
          _data.id = item.id
          _data.code = item.code
          getDictionaryAll(_data).then(res => {
            this.dictionaryList = res.data.data
          })
        }
        if(item.name === ''){
          _dtmfModeData.code = item.code
          getDictionaryAll(_dtmfModeData).then(res => {
            this.dtmfModeData = res.data.data
          })
        }
      })
    }
  },
  created() {
    this.form = this.$store.state.formPage
    this.getPbxAll(this.form)
    this.getProfileInfo()
  },
  watch: {
    dialogFormVisible(val) {
      if (val === false) {
        this.addFrom = this.$options.data().form;
      }else {
        let  dictionaryTypeList = []
        addDictionaryList().then(res => {
          dictionaryTypeList = res.data.data.records
          //ID
          this.filterGateway(dictionaryTypeList)
        })
      }
    }
  },
  mounted() {
    this.$bus.$on('pageChange', () => {
      this.form = this.$store.state.formPage
      this.getPbxAll(this.form)
    })
  }

}
</script>

<style scoped>
  .container p {
  background-color: #f2f2f2;
  padding: 10px 15px;
}

.nav-form {
  margin: 15px;
  height: 40px;
  display: flex;
  justify-content: space-between;
}

#networkManagement .width {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.width > * {
  flex: 1;
}
</style>
