<template>
  <!--  -->
  <div>
    <div class="container">
      <div class="nav">
        <div class="nav-l">
          <el-form ref="form" label-width="80px" :inline="true" :rules="form_rules"  :model="navForm">
            <el-form-item label="" prop="roleName">
              <el-input v-model="navForm.roleName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="status">
              <el-select v-model="navForm.status" placeholder="">
                <el-option
                    v-for="item in status"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <el-button type="primary" @click="find"></el-button>
          <el-button @click="clearForm"></el-button>
        </div>
        <div class="nav-r">
          <el-button type="primary" @click="addForms(null, '')"></el-button>
        </div>
      </div>

      <el-dialog title="" :visible.sync="dialogFormVisibles">
        <el-form :model="dataScopeForm">
          <el-form-item label="ID" :label-width="formLabelWidth">
            <el-input v-model="dataScopeForm.roleId"/>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="dataScopeForm.dataScope">
              <el-option :label="item.label" :value="item.value" v-for="item in dataScopeType"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitDataScopeForm(dataScopeForm)"> </el-button>
        </div>
      </el-dialog>


      <el-dialog :title="title" :visible.sync="dialogFormVisible">
        <el-form :model="addForm" ref="addForm" :rules="rules">
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="roleName"
          >
            <el-input v-model="addForm.roleName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="orderNum"
          >
            <el-input v-model="addForm.orderNum" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="roleCode"
          >
            <el-input v-model="addForm.roleCode" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth" required>
            <el-tree
                props="permission"
                :data="menuIds"
                :props="defaultProps"
                node-key="id"
                accordion
                :check-on-click-node="true"
                show-checkbox
                @check="checkChange">
            </el-tree>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio-group v-model="addForm.status">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="addForm.desc" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm"
          > 
          </el-button
          >
        </div>
      </el-dialog>

      <el-table
          v-if="list.length !== 0"
          :data="list"
          border
          style="width: 100%; margin-top: 20px">
        <el-table-column
            prop="date"
            align="center"
            label=""
            width="180">
          <template scope="scope">{{ scope.$index + 1 }}</template>
        </el-table-column>
        <el-table-column
            prop="roleName"
            align="center"
            label=""
            width="180">
        </el-table-column>
        <el-table-column
            prop="name"
            align="center"
            label=""
            width="180">
          <template scope="scope">
            <p v-if="scope.row.status === 0">
              <el-tag type="success"></el-tag>
            </p>
            <p v-else>
              <el-tag type="error"></el-tag>
            </p>
          </template>
        </el-table-column>
        <el-table-column
            prop="createTime"
            align="center"
            label=""
            width="300">
        </el-table-column>
        <el-table-column
            prop="createTime"
            align="center"
            label=""
            width="180">
          <template scope="scope">
            <el-link @click="distribution(scope.row.roleId)" type="info"></el-link>
          </template>
        </el-table-column>
        <el-table-column
            align="center"
            prop="address"
            label="">
          <template scope="scope">
            <el-link style="margin-right: 20px" type="info" @click="addForms(scope.row, '')"></el-link>
            <el-link style="margin-right: 20px" type="info" @click="removeIt(scope.row)"></el-link>
          </template>
        </el-table-column>
      </el-table>
      <my-empty v-else></my-empty>
    </div>

  </div>
</template>

<script>
import {
  addRole,
  getRoleList,
  getMenuAll,
  deleteRoleList,
  getDistribution,
  distribution, upDataRoleList
} from "@/newwork/system-colltroner";
import {menuToTree, treeToArray} from "@/uti";
import {deleteRate} from "@/newwork/ground-colltroner";
import myEmpty from "@/newwork/myEmpty";
import {isValidNumber} from "@/util/validate";


export default {
  name: 'role-manage',
  data() {
    const validateNum = (rule, value, callback) => {
      if(!isValidNumber(value)){
        callback(new Error('ip,'))
      }else {
        callback()
      }
    }
    return {
      menuIds: [],//
      formLabelWidth: '120px',
      dialogFormVisible: false,
      dialogFormVisibles: false,
      title: '',
      navForm: {
        menuIds: [],
        pageSize: 14,
        pageNum: 1,
        orderNum: '', //
        roleCode: '', //
        roleName: '', //
        status: '',
      },
      timer: {},
      addForm: {
        menuIds: [],
        pageSize: 14,
        pageNum: 1,
        orderNum: '', //
        roleCode: '', //
        roleName: '', //
        status: 0,
      },
      total: 0,
      selectMenuList: [],
      dataScopeForm: {
        dataScope: ''
      },
      list: [],
      rules: {
        roleName: [{required: true, message: '', trigger: 'change'},],
        orderNum: [{required: true, message: '', trigger: 'change'},
          {validator: validateNum, message: '', trigger: 'change'}
        ],
        roleCode: [{required: true, message: '', trigger: 'change'},
          {validator: validateNum, message: '', trigger: 'change'}

        ],
        permission: [
          {required: true, message: '', trigger: 'change'}
        ],
        status: [
          {required: true, message: '', trigger: 'change'}
        ]
      },
      form_rules:{
        roleName: [{required: false, message: '', trigger: 'change'},],
        status: [
          {required: false, message: '', trigger: 'change'}
        ]
      },
      isVacancy: true,
      defaultProps: {
        children: 'children',
        label: 'menuName'
      },
      status: [
        {label: '', value: 0},
        {label: '', value: 1},
      ],
      dataScopeType: [
        {label: '', value: 1},
        {label: '', value: 2},
        {label: '', value: 3},
        {label: '', value: 4},
        {label: '', value: 5},
      ]


    }
  },
  watch: {
    dialogFormVisible(val) {
      if (val) {
        getMenuAll().then(res => {
          this.menuIds = menuToTree(res.data.data)
          console.log(res)
        }).catch(e => {
          this.$message.error(e)
        })
      }
    }
  },
  methods: {
    distribution(row) {
      this.dialogFormVisibles = true
      this.dataScopeForm.roleId = row
    },
    submitDataScopeForm(dataScopeForm){
      distribution(dataScopeForm).then(res => {
        console.log(res);
        if(res.data.code === 200){
          this.$message.success('')
        }else {
          this.$message.error(res.data.msg)
        }
      })
    },
    addForms(row, title) {
      this.dialogFormVisible = true
      this.title = title
      this.title === '' ? this.addForm = this.$options.data().addForm : this.addForm = row
    },
    find() {
      this.getRoleList(this.navForm)
    },
    clearForm() {
      this.resetForm('form')
    },
    removeIt(row) {
      console.log(row)
      deleteRoleList(row.roleId).then(res => {
        if (res.data.code === 200) {
          this.$message.success('')
          this.getRoleList(this.navForm)
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    submitForm() {
      this.$refs.addForm.validate((valid) => {
        if (valid && !this.isVacancy) {
          if (this.title === '') {
            addRole(this.addForm).then(res => {
              if (res.data.code === 200) {
                this.$message.success('')
                this.getRoleList(this.navForm)
                this.resetForm()
                this.dialogFormVisible = false
              } else {
                this.$message.error(res.data.msg)
              }
            }).catch(e => this.$message.error(e))
          } else {
            upDataRoleList(this.addForm).then((res => {
              if(res.data.code === 200){
                this.$message.success('')
              }else {
                this.$message.error(res.data.msg)
              }
            }))
          }
        } else {
          this.$message.error(' ')
          return false;
        }
      });
    },
    resetForm(string) {
      string === 'form' ? this.$refs.form.resetFields() : this.$refs.addForm.resetFields();
      this.getRoleList(this.navForm)
    },

    checkChange(a, context) {
      this.addForm.menuIds = []
      const menuId = treeToArray(context.checkedNodes)
      menuId.forEach((item, i) => {
        this.addForm.menuIds.push(item.menuId)
      })
      this.addForm.menuIds.length === 0 ? this.isVacancy = true : this.isVacancy = false
      console.log(this.addForm.menuIds)
    },
    getRoleList(form) {
      getRoleList(form).then(res => {
        console.log(res)
        this.total = res.data.data.total
        this.$store.dispatch('total', this.total)
        const formPage = {}
        formPage.pageNum = 1;
        formPage.pageSize = 14
        this.$store.dispatch('formPage', formPage)

        this.list = res.data.data.records
      }).catch(e => {
        this.$message.error(e)
      })
    }
  },
  created() {
    this.getRoleList(this.navForm)
  },
  mounted() {
    this.$bus.$on('pageChange', () => {
      this.getRoleList(this.$store.state.formPage)
    })
  },
  components: {
    myEmpty
  },
  computed: {
    // defaultMenuId(){
    //   if(this.addForm.sysMenuList.length !== 0){
    //     this.addForm.sysMenuList.forEach((item, i) => {
    //       this.addForm.menuIds.push(item.id)
    //     })
    //     return this.addForm.sysMenuList.menuIds.id
    //   }else {
    //     return  []
    //   }
    // }
  }
}
</script>

<style scoped>
.nav {
  display: flex;
  justify-content: space-between;
}

.nav-l {
  height: 40px;
  display: flex;
}

</style>
